.calc {
    margin: 0 auto;
    width: 300px;
    height: 500px;
    border: 1px solid #000;
    border-radius: 20px;
    background: #000;
    color: #fff;
    font-family: Arial, Helvetica, sans-serif;
    padding: 18px;
    box-shadow: 4px 4px 4px #4f4f4f;
}
.calc-screen {
    height: 125px;
    padding: 10px;
    display: grid;
    justify-items: end;
    align-items: end;
    margin-bottom: 25px;
}
.calc-screen p {
    font-size: 4rem;
    margin: 0;

}
.buttons {
    display: grid;
    grid-template-areas:
    "ac plus-minus precent division"
    "seven eigth nine multiply"
    "four five six minus"
    "one two three plus"
    "zero zero dot equal";
    grid-gap: 7px;
    justify-items: center;
}
 .btn {
    width: 60px;
    height: 60px;
    background: #333;
    border-radius: 50%;
    text-align: center;
    line-height: 60px;;
    font-size: 1.5rem;
    cursor: pointer;
    user-select: none;
 }
 .btn.zero {
    grid-area: zero;
    width: 100%;
    border-radius: 34px;
 }
.btn:hover{
    filter: brightness(120%);
}

.btn:active{
    filter: brightness(90%);
}
.btn.bg-grey {
    background: #a6a6a6;
}
.btn.bg-orange {
    background: #ff9501;
}