.calc {
  width: 300px;
  height: 500px;
  color: #fff;
  background: #000;
  border: 1px solid #000;
  border-radius: 20px;
  margin: 0 auto;
  padding: 18px;
  font-family: Arial, Helvetica, sans-serif;
  box-shadow: 4px 4px 4px #4f4f4f;
}

.calc-screen {
  height: 125px;
  align-items: end;
  justify-items: end;
  margin-bottom: 25px;
  padding: 10px;
  display: grid;
}

.calc-screen p {
  margin: 0;
  font-size: 4rem;
}

.buttons {
  grid-gap: 7px;
  grid-template-areas: "ac plus-minus precent division"
                       "seven eigth nine multiply"
                       "four five six minus"
                       "one two three plus"
                       "zero zero dot equal";
  justify-items: center;
  display: grid;
}

.btn {
  width: 60px;
  height: 60px;
  text-align: center;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: #333;
  border-radius: 50%;
  font-size: 1.5rem;
  line-height: 60px;
}

.btn.zero {
  width: 100%;
  border-radius: 34px;
  grid-area: zero;
}

.btn:hover {
  filter: brightness(120%);
}

.btn:active {
  filter: brightness(90%);
}

.btn.bg-grey {
  background: #a6a6a6;
}

.btn.bg-orange {
  background: #ff9501;
}

